@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@200;300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* for project container - might not need this */
.projectContainer {
  padding-left: 3em;
  padding-right: 3em;
  margin-top: 3em;
}
.hero-image {
    /* background-image: url("src/assets/img/mark-tegethoff-lX-sxatAvqc-unsplash-1.jpg"); */
    height: 100px;
    width: 100%;
    background-position: left;
    background-repeat: no-repeat;
    background-size: cover;
    position: center;
  }

.hero-text {
    font-family: 'Barlow Condensed', sans-serif;
    text-align: center;
    position: absolute;
    top: 5%;
    left: 10%;
    transform: translate(-10%, -10%);
    font-size: 100px;
    color: rgb(109, 24, 24);
  }

